import { useState, useRef, useEffect } from 'react';
import Row from "../Row";
import Stack from '@mui/material/Stack';
import { saveGameResults, saveGameState } from '../../services/state';
import { getOptimalSolution, isSolved } from '../../services/solution';
import { logEvent } from '../../services/analytics';

interface Props {
    rows: number[][];
    width: string;
    height: string;
    scrollable: boolean;
    canPlay: boolean;
    onGameOver?: (rows: number[][]) => void;
}

const Field = (props: Props) => {
    const stackRef = useRef<HTMLDivElement>(null);
    const [rows, setRows] = useState(props.rows);
    const [onGameOver] = useState<undefined | ((rows: number[][]) => void)>(() => props.onGameOver);
    const swap = (from: number, to: number) => {
        const lastRow = rows[rows.length - 1];
        const copy = JSON.parse(JSON.stringify(rows));
        copy.push(doSwap(lastRow, from, to));
        setRows(copy);
        saveGameState(copy);
        logEvent('Swap', {distance: Math.abs(from - to)});
        if (isSolved(copy[copy.length - 1])) {
            logEvent('Game over', {swaps: copy.length - 1});
        }
    };

    const checkGameOver = () => {
        const lastRow = rows[rows.length - 1];
        if (isSolved(lastRow) && onGameOver) {
            saveGameResults(rows.length - 1, getOptimalSolution(rows[0]));
            setTimeout(onGameOver, 1000);
        }
    };
    const scrollToBottom = () => {
        setTimeout(() => {
            if (stackRef.current) {
                stackRef.current.scrollTo({
                    top: stackRef.current.scrollHeight,
                    behavior: 'smooth',            
                });
            }
        }, 500);
    };
    useEffect(scrollToBottom, [rows]);
    useEffect(checkGameOver, [rows, onGameOver]);
    return (
        <Stack direction="column" spacing={1} style={{
            justifyContent: 'center',
            alignItems: 'center',
            padding: 'min(1vw, 1vh)',
            width: props.width,
            height: '100%',
            margin: '0 auto',
            // position: 'relative'
        }}>
            {getRow(0, '🚀', rows[0], rows.length > 1 ? rows[1] : undefined, swap, props.canPlay && rows.length === 1)}
            <Stack direction="column" spacing={1} style={{
                    alignItems: 'center',
                    height: props.scrollable ? props.height : undefined,
                    overflowY: props.scrollable? 'auto' : undefined
            }} ref={stackRef}>
                {getRows(rows, swap, props.canPlay)}
            </Stack>
            <div style={{
                // position: 'absolute',
                // bottom: 0
            }}>
                <Row title={'🏁'} numbers={sorted(props.rows[0])} changedIndexes={[]} />
            </div>
        </Stack>
    );
};

const getRows = (rows: number[][], swap: (from: number, to: number) => void, canPlay: boolean) => {
    const res = [];
    for (let index = 1; index < rows.length; index++) {
        const row = rows[index];
        const nextRow = index < rows.length - 1 ? rows[index+1] : undefined;
        const isLastRow = rows.length === index + 1;
        res.push(getRow(index, `🔄`, row, nextRow, swap, canPlay && isLastRow && !isSolved(row)));
    }
    return res;
};

const getRow = (index: number, title: string, row: number[], nextRow: number[] | undefined, swap: (from: number, to: number) => void, canPlay: boolean) => {
    const changedIndexes = [];
    if (nextRow) {
        for (let i = 0; i < row.length && i < nextRow.length; i++) {
            if (row[i] !== nextRow[i]) {
                changedIndexes.push(i);
            }
        }
    }
    return (
        <Row title={title} 
            key={`row-${index}`} 
            numbers={row} 
            onSwap={canPlay ? swap : undefined} 
            changedIndexes={changedIndexes}
        />
    );
}

const sorted = (numbers: number[]): number[] => {
    const res = new Array<number>();
    for (let i = 1; i <= numbers.length; i++) {
        res.push(i);
    }
    return res;
};

const doSwap = (numbers: number[], from: number, to: number): number[] => {
    const copy: number[] = [];
    numbers.forEach(n => copy.push(n));
    [copy[from], copy[to]] = [copy[to], copy[from]];
    return copy;
};

export default Field;