import { MOVE_DISTANCE } from "../constants";

const getMinSwaps = (arr: Array<number>): Array<Array<number>> => {
    const n = arr.length;
    const target = arr.slice().sort((a: number, b: number) => a - b); // Sorted version of the array
    const visited = new Set<string>(); // To keep track of visited configurations
    const queue = [] as Array<{ arr: number[], steps: Array<number[]> }>; // BFS queue for state exploration
    
    // Function to convert array to a string for easy comparison and use in visited set
    const arrayToString = (arr: Array<number>) => arr.join(',');

    // Initial state (starting configuration)
    queue.push({ arr: arr, steps: [] });
    visited.add(arrayToString(arr));

    while (queue.length > 0) {
        const { arr, steps } = queue.shift() as {arr: number[], steps: Array<number[]>};

        // If the current state is the sorted array, return the steps
        if (arr.join(',') === target.join(',')) {
            return steps;
        }

        // Try all valid swaps (1 or 2 positions apart)
        for (let i = 0; i < n - 1; i++) {
            for (let j = i + 1; j <= i + MOVE_DISTANCE && j < n; j++) {
                // Swap elements i and j
                const newArr = arr.slice();
                [newArr[i], newArr[j]] = [newArr[j], newArr[i]];

                const newArrString = arrayToString(newArr);
                if (!visited.has(newArrString)) {
                    visited.add(newArrString);
                    queue.push({
                        arr: newArr,
                        steps: [...steps, [i, j]] // Log the swap
                    });
                }
            }
        }
    }

    return []; // Return an empty array if no solution is found
};

const getOptimalSolution = (arr: number[]): number => {
    return getMinSwaps(arr).length;
};

const calculateEfficiency = (arr: number[], solvedIn: number) => {
    return Math.round(100.0 * getOptimalSolution(arr) / solvedIn);
}

const isSolved = (numbers: number[]): boolean => {
    for (let i = 1; i < numbers.length; i++) {
        if (numbers[i - 1] >= numbers[i]) {
            return false;
        }
    }
    return true;
};

export {
    getMinSwaps,
    getOptimalSolution,
    calculateEfficiency,
    isSolved
};