import Paper from '@mui/material/Paper';
import {forwardRef} from 'react';

interface Props {
    position: number;
    n: number;
    onClick?: () => void | undefined;
    selected?: boolean;
    changedOnLastMove?: boolean;
    canMoveTo?: boolean;
}

const Cell = forwardRef<HTMLDivElement, Props>((props, ref) => {
    let backgroundColor = undefined;
    if (props.selected) {
        backgroundColor = '#03a9f4';
    } else if (props.changedOnLastMove) {
        backgroundColor = '#ff9800';
    } else if (props.position === props.n) {
        backgroundColor = '#4caf50'; //#1976d2
    }
    return (
        <Paper sx={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                fontSize: 'min(3vw, 3vh)',
                backgroundColor: backgroundColor,
                borderRadius: props.canMoveTo? '30%' : undefined
            }} 
            onClick={props.onClick}
            ref={ref} 
        >
            {numberComponent(props)}
        </Paper>
    );
});

Cell.displayName = 'Cell';

const numberComponent = (props: Props) => {
    const style = {
        color: props.position === props.n ? 'white': 'black',
    };
    return (
        <span style={style}>{props.n}</span>
    )
}

export default Cell;