import React from 'react';
import { Button, Stack } from '@mui/material';
import TwitterIcon from '@mui/icons-material/Twitter';
import FacebookIcon from '@mui/icons-material/Facebook';

interface Props {
    text: string;
    url: string;
}

const ShareButtons = ({ url, text }: Props) => {
  const twitterShareUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(text)}&url=${encodeURIComponent(url)}`;
  const facebookShareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`;

  return (
    <Stack direction="row" spacing={2} justifyContent={'center'}>
      <Button
        variant="contained"
        color="primary"
        startIcon={<TwitterIcon />}
        onClick={() => window.open(twitterShareUrl, '_blank', 'noopener,noreferrer')}
      >
        Twitter
      </Button>

      <Button
        variant="contained"
        color="primary"
        startIcon={<FacebookIcon />}
        onClick={() => window.open(facebookShareUrl, '_blank', 'noopener,noreferrer')}
      >
        Facebook
      </Button>
    </Stack>
  );
};

export default ShareButtons;
