import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import Typography from '@mui/material/Typography';
import { getMinSwaps, isSolved } from '../../services/solution';
import { getGameState, getStreak } from '../../services/state';
import { Button, DialogActions, DialogContent } from '@mui/material';
import { useEffect, useState } from 'react';
import ShareButtons from '../ShareButtons';

interface Props {
    onClose: () => void;
}

const Results = (props: Props) => {
    const [solved, setSolved] = useState(false);
    const [solutionLength, setSolutionLength] = useState(0);
    const [optimalSolutionLength, setOptimalSolutionLength] = useState(0);
    const [currentStreak, setCurrentStreak] = useState(0);
    useEffect(() => {
        const rows = getGameState();
        const firstRow = rows[0];
        const lastRow = rows[rows.length - 1];
        if (isSolved(lastRow)) {
            setSolved(true);
            setSolutionLength(rows.length - 1); //last row is the solution
            setOptimalSolutionLength(getMinSwaps(firstRow).length);
        } else {
            setSolved(false);
        }
        setCurrentStreak(getStreak());
    }, []);

    return (
        <Dialog onClose={props.onClose} open={true}>
            <DialogTitle>Results</DialogTitle>
            <DialogContent>
                {
                    solved && (
                        <>
                            <Typography variant="h6" component="div" textAlign={'center'}>
                                🎉🎉🎉 Congratulations! 🎉🎉🎉
                            </Typography>
                            <Typography variant="body2" component="div" textAlign={'center'}>
                                Today you solved the puzzle in <b>{solutionLength}</b> swaps with <b>{Math.round(100.0 * optimalSolutionLength / solutionLength)}%</b> efficiency.
                            </Typography>
                            <Typography variant="body2" component="div" textAlign={'center'}>
                                Optimal solution is <b>{optimalSolutionLength}</b> swaps.
                            </Typography>
                            <Typography variant="body2" component="div" textAlign={'center'}>
                                Share your results:
                            </Typography>
                            <Typography variant="body2" component="div" textAlign={'center'}>
                                <ShareButtons text={`I solved puzzle with ${solutionLength} swaps with ${Math.round(100.0 * optimalSolutionLength / solutionLength)}% efficiency`} url={window.location.href} />
                            </Typography>
                            <Typography variant="body2" component="div" textAlign={'center'}>
                                See you tomorrow for the next game!
                            </Typography>
                        </>
                    )
                }
                {
                    !solved && (
                        <Typography variant="subtitle1" component="div" textAlign={'center'}>
                            Still waiting for your solution!
                        </Typography>
                    )
                }
                <Typography variant="subtitle1" component="div" textAlign={'center'}>
                    Your game streak is <b>{currentStreak}</b>.
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button onClick={props.onClose}>Close</Button>
            </DialogActions>
        </Dialog>
    );
};

export default Results;