import { N } from "../constants";
import { getInitialArray } from "./seed";

const dateAsString = (dt: Date = new Date()): string => {
    return dt.toDateString();
};

const getLastPlayed = (): string | null => {
    return localStorage.getItem('last_played');
};

const setLastPlayed = (today: string) => {
    localStorage.setItem('last_played', today);
}

const getGameState = (): number[][] => {
    const today = dateAsString();
    const state = localStorage.getItem('state');
    if (getLastPlayed() !== today || !state) {
        const rows = [getInitialArray(N)];
        setLastPlayed(today);
        saveGameState(rows);
        return rows;
    } else {
        return JSON.parse(state);
    }
};

const saveGameState = (rows: number[][]) => {
    localStorage.setItem('state', JSON.stringify(rows));
};

const getGameResults = (): {[key: string]: {solution: number, optimal: number}} => {
    const stats = localStorage.getItem('stats');
    if (stats) {
        return JSON.parse(stats);
    } else {
        return {};
    }
};

const saveGameResults = (solutionLength: number, optimalSolutionLength: number): void => {
    const stats = getGameResults();
    stats[new Date().toDateString()] = {
        solution: solutionLength,
        optimal: optimalSolutionLength
    };
    localStorage.setItem('stats', JSON.stringify(stats));
};

const getStreak = (): number => {
    const stats = getGameResults();
    let streak = 0;
    const date = new Date();
    // Today's game might be still in progress
    if (!stats[dateAsString(date)]) {
        date.setDate(date.getDate() - 1);
    }
    while (stats[dateAsString(date)]) {
        date.setDate(date.getDate() - 1);
        streak++;
    }
    return streak;
};

export {
    getGameState, saveGameState, saveGameResults, getStreak
}