import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import Typography from '@mui/material/Typography';
import Field from '../Field';
import { getMinSwaps } from '../../services/solution';
import { Button, DialogActions, DialogContent } from '@mui/material';
import { helpShown } from '../../services/help';

interface Props {
    onClose: () => void;
}

const Help = (props: Props) => {
    helpShown();
    return (
        <Dialog onClose={props.onClose} open={true}>
            <DialogTitle>How to play?</DialogTitle>
            <DialogContent>
                <Typography variant="body2" component="div" textAlign={'center'}>
                    Goal of the game - <b>swap numbers</b> to sort them in ascending order. You can swap adjacent number or 1 or 2 apart.
                </Typography>
                <Typography variant="body2" component="div" textAlign={'center'}>
                    Example:
                </Typography>
                <Field rows={getRows()} width='100%' height='35vw' scrollable={false} canPlay={false}/>
                <Typography variant="h6" component="div" textAlign={'center'}>
                    How many swaps do you need for 7 numbers?
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button onClick={props.onClose}>Close</Button>
            </DialogActions>
        </Dialog>
    );
};

const getRows = (): number[][] => {
    const initial = [3, 4, 2, 1];
    const solution = getMinSwaps(initial);
    const rows = [initial];
    const arr = JSON.parse(JSON.stringify(initial));
    solution.forEach(move => {
        const a = move[0], b = move[1];
        [arr[b], arr[a]] = [arr[a], arr[b]];
        rows.push(JSON.parse(JSON.stringify(arr)));
    })

    return rows;
};

export default Help;