import Cell from "../Cell";
import { useState, useRef, RefObject } from 'react';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { MOVE_DISTANCE, N } from '../../constants';
import { logEvent } from '../../services/analytics';

interface Props {
    title: string;
    numbers: number[];
    changedIndexes: number[];
    onSwap?: (from: number, to: number) => void | undefined;
}

const Row = (props: Props) => {
    const [selectedCell, setSelectedCell] = useState<number>(-1);
    const cellRefs = useRef<(HTMLDivElement | null)[]>([]);
    const rowRef = useRef<HTMLDivElement>(null);

    const clickCell = (index: number) => {
        if (selectedCell === -1) {
            setSelectedCell(index);
            logEvent('Select Cell');
        } else if (selectedCell === index) {
            setSelectedCell(-1);
            logEvent('Unselect Cell');
        } else if (Math.abs(selectedCell - index) <= MOVE_DISTANCE) {
            setSelectedCell(-1);
            props.onSwap && props.onSwap(selectedCell, index);
        }
    };
    return (
        <Paper style={{margin: 'min(1vw, 1vh)'}} elevation={4} ref={rowRef}>
            {props.onSwap && hints(selectedCell, rowRef, cellRefs)}
            <Box sx={{
                display: 'flex',
                flexWrap: 'wrap',
                '& > :not(style)': {
                    m: 1,
                    width: 'min(5vw, 5vh)',
                    height: 'min(5vw, 5vh)',
                },
                // padding: 'min(1vw, 1vh)',
                justifyContent: 'center',
                alignItems: 'center',
                backgroundColor: !props.onSwap ? '#eeeeee' : 'none'
            }}>
                <span>{props.title}</span>
                {props.numbers.map((n, index) => 
                    (
                        <Cell key={`cell-${index}`}
                            ref={(el : HTMLDivElement | null) => {
                                cellRefs.current[index] = el;
                            }}
                            n={n} 
                            position={index + 1} 
                            selected={selectedCell === index} 
                            changedOnLastMove={props.changedIndexes.indexOf(index) >= 0}
                            onClick={props.onSwap ? () => clickCell(index) : undefined} 
                            canMoveTo={selectedCell !== -1 && selectedCell !== index && Math.abs(selectedCell - index) <= MOVE_DISTANCE}
                        />
                    )
                )}

            </Box>
        </Paper>
    );
};

const hints = (selectedCell: number, rowRef: RefObject<HTMLDivElement | null>, cellRefs: RefObject<(HTMLDivElement | null)[]>) => {
    if (selectedCell === -1) {
        return <div style={{height: '40px'}}></div>
    }
    return (
        <Box sx={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            alignItems: 'center',
        }}>
            <svg style={{
                width: '100%',
                height: '40px',
            }}
            >
                <defs>
                    <marker id="arrowhead-down" markerWidth="6" markerHeight="6"
                        refX="3" refY="3" markerUnits="userSpaceOnUse">
                      <polygon points="0,0 6,0 3,6" fill="black" />
                    </marker>
                </defs>
                {[-2, -1, 1, 2].filter(i => {
                    return (i + selectedCell) >= 0 && (i + selectedCell < N);
                }).map(i => {
                    return arrow(rowRef.current, cellRefs.current[selectedCell], cellRefs.current[selectedCell + i], i)
                })}
            </svg>
        </Box>
    );
}

const findCenterX = (parent: HTMLDivElement | null, child: HTMLDivElement | null) => {
    if (!parent || !child) {
        return 0;
    }
    return child.getBoundingClientRect().left - 
            parent.getBoundingClientRect().left + 
            child.getBoundingClientRect().width / 2;
};

const arrow = (parent: HTMLDivElement | null, from: HTMLDivElement | null, to: HTMLDivElement | null, distance: number) => {
    const fromDivCenter = findCenterX(parent, from);
    const toDivCenter = findCenterX(parent, to);
    const offset = Math.sign(distance) * ((MOVE_DISTANCE - Math.abs(distance)) * 10 + 3);

    const x1 = fromDivCenter + offset;
    const x2 = toDivCenter;
    return arrowComponent(Math.min(x1, x2), Math.max(x1, x2), 5 + Math.abs(distance) * 10);
};

const arrowComponent = (x1: number, x2: number, h: number) => {
    const curve = 10;
    const y = 35;
    return (
        <path key={`arrow-${x1}-${x2}`}
        d={`
                M ${x1} ${y}
                L ${x1} ${y - h + curve}
                A ${curve} ${curve} 0 0 1 ${x1 + curve} ${y - h}
                L ${x2 - curve} ${y - h}
                A ${curve} ${curve} 0 0 1 ${x2} ${y - h + curve}
                L ${x2} ${y}
        `}
        stroke={'black'}
        fill={'none'}
        strokeWidth={2}
        markerStart={'url(#arrowhead-down)'}
        markerEnd={'url(#arrowhead-down)'}
        />
    );
};

export default Row;