const factorial = (n: number): number => {
    let res = 1;
    for (let i = 1; i <= n; i++) {
        res = res * i;
    }
    return res;
}

const getKthPermutation = (n: number, k: number): Array<number> => {
    const nums = Array.from({ length: n }, (_, i) => i + 1);

    k--; // Convert to 0-based index
    const result = [];

    for (let i = n; i >= 1; i--) {
        const idx = Math.floor(k / factorial(i - 1));
        result.push(nums[idx]);
        nums.splice(idx, 1); // Remove used number
        k %= factorial(i - 1);
    }

    return result;
}

const getDaySeed = (min: number, max: number, today = new Date()): number => {
    const seed = `${today.getFullYear()}-${today.getMonth()}-${today.getDate()}`;

    // Simple hash function to convert date string to a seed
    let hash = 0;
    for (let i = 0; i < seed.length; i++) {
        hash = seed.charCodeAt(i) + ((hash << 5) - hash);
    }

    // Convert hash to a [0, 1) float
    const random = Math.abs(Math.sin(hash)) % 1;

    // Scale to desired range
    return Math.floor(min + random * (max - min));    
}

const getInitialArray = (n: number, date: Date = new Date()): Array<number> => {
    const totalCombinations = factorial(n);
    let seed = getDaySeed(1, totalCombinations, date);
    const yesterday = new Date(date);
    yesterday.setDate(date.getDate() - 1);
    const prevSeed = getDaySeed(1, totalCombinations, yesterday);
    if (seed === prevSeed) {
        const yearAgo = new Date(date);
        yearAgo.setDate(date.getDate() - 365 + date.getDay());
        seed = getDaySeed(0, totalCombinations, yearAgo);
    }
    return getKthPermutation(n, seed);
}

export {
    getKthPermutation,
    getInitialArray
}