import './App.css';
import { useEffect, useState } from 'react';
import Field from './components/Field';
import Header from './components/Header';
import Help from './components/Help';
import Footer from './components/Footer';
import { getGameState } from './services/state';
import Results from './components/Results';
import { shouldShowHelp } from './services/help';
import { initGA, logEvent, logPageView } from './services/analytics';

const App = () => {
  const [helpOpen, setHelpOpen] = useState(false);
  const [resultsOpen, setResultsOpen] = useState(false);
  const rows = getGameState();
  useEffect(() => {
    if (shouldShowHelp()) {
      setHelpOpen(true);
    }
    initGA();
    logPageView('/');
  }, []);

  const gameOver = () => {
    setResultsOpen(true);
  };

  const openHelp = () => {
    setHelpOpen(true);
    logEvent('Open Help');
  };

  const openResults = () => {
    setResultsOpen(true);
    logEvent('Open Results');
  };

  return (
    <div className="App">
      <Header onHelpOpen={openHelp} onResultsOpen={openResults}/>
      <Field 
        rows={rows} 
        width={'80vw'} 
        height={'60vh'} 
        scrollable={true} 
        canPlay={true} 
        onGameOver={gameOver}
      />
      {helpOpen && <Help onClose={() => setHelpOpen(false)}/>}
      {resultsOpen && <Results onClose={() => setResultsOpen(false)}/>}
      <Footer />
    </div>
  );
}

export default App;
